import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import List from '@/components/List'

import Take from '@/components/Take'
import TakeVou from '@/components/TakeVou'
import TakePhoto from '@/components/TakePhoto'

import Inco from '@/components/Inco'
import IncoVou from '@/components/IncoVou'
import IncoPhoto from '@/components/IncoPhoto'

Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
    },
    {
      path: '/List',
      name: 'List',
      component: List
    },
    {
      path: '/Take',
      name: 'Take',
      component: Take,
    },
    {
      path: '/TakeVou',
      name: 'TakeVou',
      component: TakeVou
    },
    {
      path: '/TakePhoto',
      name: 'TakePhoto',
      component: TakePhoto
    },
    {
      path: '/Inco',
      name: 'Inco',
      component: Inco
    },
    {
      path: '/IncoVou',
      name: 'IncoVou',
      component: IncoVou
    },
    {
      path: '/IncoPhoto',
      name: 'IncoPhoto',
      component: IncoPhoto
    }
  ]
})



